import './dip.less'
import { Link } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import React, { useState, useEffect } from 'react'
import Button from '../../components/Button/Button'
import { useAlert } from 'react-alert'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import ApiProxy from '../../model/ApiProxy'
import backIcon from '../../assets/img/back.svg'
import axios from 'axios'
import { bg, findMaxMinSumIndex, getAmountETHAndDeri } from '../../utils/utils'
import { useCallback } from 'react'
import useContract from '../../hooks/useContractLp'
import { BigNumber } from 'ethers'

function DipSix({ wallet, lang }) {
  const [value, setValue] = useState()
  const [optionInfo, setOptionInfo] = useState([])
  const [sumPower, setSumPower] = useState(0)
  const [winner, setWinner] = useState()
  const [deadline, setDeadline] = useState(false)
  const [disabled, setDisabled] = useState(true)
  const [isOptioned, setIsOptioned] = useState(true)
  const [votingPower, setVotingPower] = useState(0)
  const [useVotingPower, setUseVotingPower] = useState(0)
  const [timeAgo, setTimeAgo] = useState("")
  const [ArbUniDeri, setArbUniDeri] = useState(0)
  const alert = useAlert();
  const NFTContract = useContract("IERC721", 42161)
  const BTokenContract = useContract("LpTokenId", 42161)
  const PairContract = useContract("PairAbi", 42161)
  const PoolContract = useContract("arbitrumLp", 42161)
  const PairAddress = "0x96C3966534b4377691e11e163A0409Ad01667343"

  const selectOption = useCallback((value) => {
    if (!isOptioned) {
      let radioI = document.getElementById("I")
      let radioII = document.getElementById("II")
      let radioIII = document.getElementById("III")
      let radioVI = document.getElementById("VI")
      if (value === "1") {
        radioI.click()
      } else if (value === "2") {
        radioII.click()
      } else if (value === "3") {
        radioIII.click()
      } else if (value === "4") {
        radioVI.click()
      }
      setValue(value)
    }

  }, [isOptioned])
  const getUserVotingPower = async () => {
    let res = await ApiProxy.request("getUserVotingPower", [wallet.detail.account])
    setVotingPower(res)
  }
  const getUserVotingResult = async () => {
    let res = await ApiProxy.request("getUserVotingResult", [wallet.detail.account])
    console.log("getUserVotingResult", res)
    if (res.option !== "0") {
      setValue(res.option)
      setIsOptioned(true)
    } else {
      // setIsOptioned(false)
    }
  }

  const getArbUniDeri = useCallback(async () => {
    let NFTNumber = await NFTContract.balanceOf(wallet.detail.account)
    NFTNumber = BigNumber.from(NFTNumber._hex).toString()
    let tokenId = []
    for (let index = 0; index < NFTNumber; index++) {
      let id = await NFTContract.tokenOfOwnerByIndex(wallet.detail.account, index)
      id = BigNumber.from(id._hex).toString()
      tokenId.push(id)
    }
    let deriTokenId = [];
    for (let index = 0; index < tokenId.length; index++) {
      let id = await PoolContract.getPositionInfo(tokenId[index])
      if (id[0].toLocaleLowerCase() === PairAddress.toLocaleLowerCase()) {
        deriTokenId.push(tokenId[index])
      }
    }
    let data = deriTokenId.map(async (item) => {
      let obj = {}
      let res = await BTokenContract.positions(item)
      obj = {
        tickLower: res.tickLower,
        tickUpper: res.tickUpper,
        liquidity: BigNumber.from(res.liquidity._hex).toString()
      }
      return obj
    })
    data = await Promise.all(data)
    let tickCurrent = await PairContract.slot0()
    tickCurrent = tickCurrent.tick
    let amount = data.map((item) => {
      let [total_amount0, total_amount1] = getAmountETHAndDeri(tickCurrent, item.tickLower, item.tickUpper, item.liquidity)
      return {
        deri: total_amount0 / 10 ** 18,
        eth: total_amount1 / 10 ** 18
      }
    })
    let deriSum = amount.reduce((accumulator, currentValue) => {
      return accumulator + currentValue.deri;
    }, 0)
    console.log("getArbUniDeri", deriSum)
    setArbUniDeri(deriSum)
  }, [BTokenContract, PairContract, NFTContract, wallet.detail.account, PoolContract])

  const getVotingResult = async () => {
    let res = await axios.get("https://vote.deri.io/get_voting_powers")
    console.log("getVotingResult", JSON.parse(res.data[0].votes))
    if (res.data && res.data[0].votes) {
      let timeNow = new Date()
      let MinNow = timeNow.getMinutes()
      let hoverNow = timeNow.getHours()
      let stemp = res.data[0].timestamp * 1000
      let timeAgo = new Date(stemp)
      let MinAgo = timeAgo.getMinutes()
      let hoverAgo = timeAgo.getHours()
      let timeMin = MinNow - MinAgo
      let timeHover = hoverNow - hoverAgo
      if (timeMin >= 1 && timeHover <= 0) {
        setTimeAgo(timeMin)
      } else if (timeHover > 0) {
        let min = 60 - MinAgo;
        let minTime = min + Number(MinNow);
        setTimeAgo(minTime)
      } else if (timeMin < 1) {
        let text = 'less than 1'
        setTimeAgo(text)
      }
      res = JSON.parse(res.data[0].votes)
      res = res.map(item => {
        return bg(item).div(10 ** 18).toNumber()
      })
      let { max, min, sum, maxIndex } = findMaxMinSumIndex(res)
      setSumPower(sum)
      setOptionInfo(res)
    }
  }

  const getVotingInfo = async () => {
    let res = await ApiProxy.request("getVotingInfo")
    console.log("getVotingInfo", res)
    if (res && res.length) {
      let time = res.find(i => +i.chainId === +wallet.detail.chainId)
      time = time.deadline + "000"
      let timestamp = new Date()
      if (timestamp.getTime() >= +time) {
        setDeadline(true)
      } else {
        setDeadline(false)
      }
    }

  }

  const vote = async () => {
    console.log([wallet.detail.chainId, wallet.detail.account, value])
    let res = await ApiProxy.request("vote", [wallet.detail.chainId, wallet.detail.account, value], { includeResponse: true })
    if (res.success) {
      getUserVotingPower()
      getUserVotingResult()
      getVotingResult()
      alert.success("Success")
    } else {
      alert.error("Failed")
    }

  }
  useEffect(() => {
    if (wallet.isConnected()) {
      getUserVotingPower()
      // getUserVotingResult()
      getVotingInfo()
    }
  }, [wallet, wallet.detail])
  useEffect(() => {
    getVotingResult()
  }, [])
  useEffect(() => {
    if (wallet.isConnected() && value && !isOptioned && !deadline) {
      setDisabled(false)
    } else {
      setDisabled(true)
    }
  }, [wallet.detail, value, optionInfo, isOptioned])

  useEffect(() => {
    let sum = 0
    if (ArbUniDeri && votingPower) {
      sum = bg(ArbUniDeri).plus(votingPower).toNumber()
    } else if (votingPower) {
      sum = votingPower
    }
    setUseVotingPower(sum)
  }, [ArbUniDeri, votingPower])

  useEffect(() => {
    if (NFTContract && wallet.detail.account && PoolContract && BTokenContract && PairContract) {
      getArbUniDeri()
    }
  }, [NFTContract, wallet.detail.account, PoolContract, BTokenContract, PairContract])
  return (
    <div className='dip-box'>
      <div className='back-governance'>
        <Link to='/governance'>
          <div className='icon-back'>
            <img alt='' src={backIcon} />
            <span>{lang['back']}</span>
          </div>
        </Link>
        <div className='poll-detail'>
          {lang['poll-detail']}
        </div>
      </div>
      <div className='dip-box-info'>
        <div className='poll-dip-info '>
          <div className='dip-list-title'>
            <div className='title-text'>
              {lang['dip']}-006
            </div>
            <div className='dip-progress'>
              <div className='executed-bg'></div>
              FINISHED
            </div>
          </div>
          <div className='dip-list-des '>
            Vote for Deri V4’s First i-Chain
          </div>
          <div className='dip-time'>
            Target End Time: 2023 Sep 8th 10:00:00AM UTC (subject to extension, see below for details)
          </div>
        </div>
        <div className='dip-option'>
          <div className={value === "1" ? "option-check option-checked" : "option-check"} onClick={(e) => selectOption("1")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='1'
                  id="I"
                  onChange={event => selectOption("1")}
                  checked={value === "1"}
                />
                <label for="I"></label>
                <span className='option-title-text'>
                  Arbitrum
                </span>
              </div>
              <div className='option-num'>
                1447532 {lang['deri-voting']} (64%)
                {/* {optionInfo[0] ? optionInfo[0].toFixed(0) : 0} {lang['deri-voting']} ({sumPower && optionInfo[0] ? ((optionInfo[0] / sumPower) * 100).toFixed(0) : 0}%) */}
              </div>
            </div>
            <div className='option-peng-bg' style={{ width: `64%` }}></div>
          </div>
          <div className={value === "2" ? "option-check option-checked" : "option-check"} onClick={() => selectOption("2")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='2'
                  id="II"
                  onChange={event => selectOption("2")}
                  checked={value === "2"}
                />
                <label for="II"></label>
                <span className='option-title-text'>
                  BNB Chain
                </span>
              </div>
              <div className='option-num'>
                214422 {lang['deri-voting']} (9%)
                {/* {optionInfo[1] ? optionInfo[1].toFixed(0) : 0}  {lang['deri-voting']} ({sumPower && optionInfo[1] ? ((optionInfo[1] / sumPower) * 100).toFixed(0) : 0}%) */}
              </div>
            </div>
            <div className='option-peng-bg ' style={{ width: `9%` }}></div>
          </div>
          <div className={value === "3" ? "option-check option-checked" : "option-check"} onClick={() => selectOption("3")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='2'
                  id="III"
                  onChange={event => selectOption("3")}
                  checked={value === "3"}
                />
                <label for="III"></label>
                <span className='option-title-text'>
                  Polygon zkEVM
                </span>
              </div>
              <div className='option-num'>
                286756 {lang['deri-voting']} (13%)
                {/* {optionInfo[2] ? optionInfo[2].toFixed(0) : 0}  {lang['deri-voting']} ({sumPower && optionInfo[2] ? ((optionInfo[2] / sumPower) * 100).toFixed(0) : 0}%) */}
              </div>
            </div>
            <div className='option-peng-bg ' style={{ width: `13%` }}></div>
          </div>
          <div className={value === "4" ? "option-check option-checked" : "option-check"} onClick={() => selectOption("4")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='2'
                  id="VI"
                  onChange={event => selectOption("4")}
                  checked={value === "4"}
                />
                <label for="VI"></label>
                <span className='option-title-text'>
                  zkSync Era
                </span>
              </div>
              <div className='option-num'>
              311015 {lang['deri-voting']} (14%)
                {/* {optionInfo[3] ? optionInfo[3].toFixed(0) : 0}  {lang['deri-voting']} ({sumPower && optionInfo[3] ? ((optionInfo[3] / sumPower) * 100).toFixed(0) : 0}%) */}
              </div>
            </div>
            <div className='option-peng-bg ' style={{ width: `14%` }}></div>
          </div>
          {/* <div className='power-time'>
            Last updated: {timeAgo} minutes ago
          </div> */}
          <div className='vote-button'>
            {/* <div className='your-power'>
              Your Voting Power :
              <DeriNumberFormat value={useVotingPower} decimalScale={2} /> DERI
            </div> */}
            <Button disabled={true} label={lang['vote']} onClick={vote} width='286' fontSize='18' />
          </div>
        </div>
        <div className='dip-option-des-rules'>
          <div className='description-close-open'>
            {lang['description']}
          </div>
          <div className='description-text'>
            <div className='ul-description'>
              The “i-Chain”, one of the two main components of Deri V4, represents the requesting interface and serves as a pivotal entry point for user interactions. It adeptly handles all the user requests, from traders initiating orders and managing margins to liquidity providers adding or removing liquidity. While theoretically any layer 1 or layer 2 (e.g. Arbitrum, BNBChain,Polygon zkEVM, and zkSync Era) can be adopted as an i-Chain, in practice, the community will choose which ones to deploy.
            </div>
          </div>

          <div className='vote-rules'>
            <div className='rules-title'>
              {lang['voting-rules']}
            </div>
            <div className='rules-text'>
              <div className='rules-margin'>
                Your voting power is the summation of the following balances in your wallet as of the deadline:
                <ul className='no-margin'>
                  <li>DERI balance</li>
                  <li>the corresponding DERI amount of your DERI-USDT SLP balance on SushiSwap</li>
                  <li>the corresponding DERI amount of your DERI-BUSD CLP balance on PancakeSwap</li>
                  <li>the corresponding DERI amount of your DERI-WETH LP balance on Uniswap V3</li>
                  <li>the corresponding DERI amount of your DLT balance in the DERI-based Inno Zone pools (BNB Chain)</li>
                </ul>
              </div>
              <div className='rules-margin'>
                You can choose any of the 3 chains (ETH/Arbitrum/BNBChain) to sign your vote by the deadline
              </div>
              <div className='rules-margin'>
                Voting Deadline = 2023 Sep 8th 10:00:00AM UTC
              </div>
              <div className='rules-margin'>
                When a vote is submitted less than 15 min to the deadline, the deadline will be extended by 15 min. That is, the voting period will continue to extend until there is no vote within the last 15 min.
              </div>
              <div className='rules-margin'>
                A snapshot will be taken at the block, at the deadline, and used to calculate the result.
              </div>
              <div className='rules-margin'>
                Please note that, if you had voted before the deadline but then had transferred your DERI, SLP, PANCAKE-LP, Uniswap V3 LP or DLT before the deadline, it would be the balance at the deadline that counts.
              </div>
            </div>
          </div>
          <div className='supplementary-notes'>
            <div className='notes-title'>
              Reference Poll Link
            </div>
            <div className='notes-text'>
              <a rel="noreferrer" target="_blank" href="https://snapshot.org/#/deri.eth/proposal/0x3eb5a59b02d1f038ebcead8405e442632ecdd835552e076b3066ab9fc185fbb9">Snapshot</a>
            </div>
          </div>
          <div className='supplementary-notes'>
            <div className='notes-title'>
              Discussion
            </div>
            <div className='notes-text'>
              <a rel="noreferrer" target="_blank" href="https://discord.com/invite/kb8ZbYgp8M">Discord</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet')(observer(DipSix))


